.slimScrollDiv {
  position: relative;
  overflow: hidden;
  width: auto;
  height: 100%;
  margin-top: 20px;
}

.logo-image {
  display: inline-block;
  height: 37px;
}

.page-sidebar-inner {
  overflow: hidden;
  width: auto;
  height: 100%;
}

.slimScrollBar {
  background: rgb(204, 204, 204);
  width: 6px;
  position: absolute;
  top: 0px;
  opacity: 0.2;
  display: none;
  border-radius: 0px;
  z-index: 99;
  right: 0px;
  height: 875px;
}

.slimScrollRail {
  width: 6px;
  height: 100%;
  position: absolute;
  top: 0px;
  display: none;
  border-radius: 0px;
  background: rgb(51, 51, 51);
  opacity: 0.2;
  z-index: 90;
  right: 0px;
}



